@import "_varivables";

.get-started-screen {
    background-color: map-get(
            $map: $theme-colors,
            $key: "primary_light_04"
    );
    padding: 24px;
    .terms {
        &__check-box {
            width: 22px;
            height: 22px;
            margin-right: 8px;
            background: red;
        }
        &__text {
            span {
                color: map-get(
                    $map: $theme-colors,
                    $key: "info" 
                );
            }
        }
    }}